import React, { useState } from "react";
import { useHistory } from "react-router";
import { Button, Header, Icon, Message, Step } from "semantic-ui-react";
import MealForm from "../../../components/MealForm/MealForm";
import getDateString from "../../../services/utils/getDateString";
import postMeal from "../../../services/backend/postMeal";
import useCanteen from "../../../services/hooks/useCanteen";
import CategoryForm from "../../../components/CategoryForm/CategoryForm";
import Container from "../../../components/Container/Container";

export default function CreateMeal() {
  const history = useHistory();
  const [meal, setMeal] = useState({
    name: "",
    date: getDateString(),
    price: "",
  });
  const [canteen] = useCanteen();
  const [globalMeal, setGlobalMeal] = useState();
  const createMeal = async () => {
    const _meal = await postMeal({ ...meal, canteen_id: canteen.id });
    setGlobalMeal(_meal);
  };

  return (
    <Container>
      {" "}
      <Step.Group widths={3}>
        <Step link>
          <Icon name="food" />
          <Step.Content>
            <Step.Title>Gericht erstellen</Step.Title>
          </Step.Content>
        </Step>
        <Step active link>
          <Icon name="leaf" />
          <Step.Content>
            <Step.Title>Kategorien hinzufügen</Step.Title>
          </Step.Content>
        </Step>
        <Step disabled link>
          <Icon name="info" />
          <Step.Content>
            <Step.Title>Bestätigen</Step.Title>
          </Step.Content>
        </Step>
      </Step.Group>
      <Header size="large">
        {globalMeal
          ? `"${globalMeal.name}" Kategorien hinzufügen`
          : `Neues Gericht anlegen`}
      </Header>
      {!globalMeal && (
        <>
          <Message>
            Tragen Sie den Namen und Preis des Gerichtes ein. Wählen Sie das
            Datum aus an dem das Gericht erscheinen soll und klicken Sie
            anschließend auf "Weiter".
          </Message>
          <MealForm meal={meal} setMeal={setMeal} />
          <br />
          <Button
            icon="send"
            negative
            onClick={() => {
              history.push("/");
            }}
          >
            Abbrechen
          </Button>
          <Button icon="send" positive onClick={createMeal}>
            Weiter
          </Button>
        </>
      )}
      {globalMeal && (
        <>
          <CategoryForm mealId={globalMeal.id} />
          <br />
          <Button positive onClick={() => history.push("/")}>
            Fertig
          </Button>
        </>
      )}
    </Container>
  );
}
